.TypeFilters-container {
  width: 80%;
  margin: auto;
  text-align: center;
}

.TypeFilters-results {
  padding: 0 10rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.TypeFilter-pokeBtn {
  background: -webkit-gradient(linear, right top, left top, from(rgba(255, 255, 255, 1)), color-stop(50%, rgba(255, 255, 255, 1)), color-stop(47%, rgba(231, 56, 39, 1)), to(rgba(231, 56, 39, 1)));
  background: -webkit-linear-gradient(right, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(231, 56, 39, 1) 47%, rgba(231, 56, 39, 1) 100%);
  background: -o-linear-gradient(right, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(231, 56, 39, 1) 47%, rgba(231, 56, 39, 1) 100%);
  background: linear-gradient(to left, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 255, 1) 50%, rgba(231, 56, 39, 1) 47%, rgba(231, 56, 39, 1) 100%);
  height: 3rem;
  width: 10rem;
  padding: 1rem;
  margin: 1rem;
  text-align: center;
  text-transform: capitalize;
  border: none;
  outline: none;

}



